.input-search{
    border-radius: 8px;
    border: 1px solid var(--Gray-700, #0D0D0D);
    background: var(--Gray-500, #262626);
    color: var(--Gray-300, #808080);
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    padding: 25px;
    width: 100%;
    margin-top: -2em;
}
.input-search:focus{
    outline-color: #4EA8DE;
}
